// 
// The Foundation Desktop Style
// 

// colors
$off-white: #fffaef;
$dark-grayish-green: #a5b1a1;
$very-dark-gray: #242424;

$box-width: 200px;

.foundation-container {
    width: 100vw;
    height: 90vh;
    color: $very-dark-gray;

    display: grid;
    grid-template-columns: 74px repeat(3, minmax(0, 1fr));
    grid-template-rows: minmax(0, 1fr) 260px minmax(0, 1fr);

    .foundation-title {
        grid-column: 4;
        grid-row: 2;

        display: grid;
        grid-template-rows: 85px 20px 36px;
        grid-template-columns: 100%;

        opacity: 0;
        transform: translateY(150px);
        transition:
            opacity 1s ease-in-out,
            transform 1s ease-in-out;

        &.open {
            opacity: 1;
            transform: translateY(0);
        }

        &.close {
            opacity: 0;
            transform: translateY(-150px);
        }

        .f-number {
            grid-row: 1;
            justify-self: end;

            font-size: 100px;
            font-weight: 700;
            font-style: italic;
            letter-spacing: -5px;
            color: $dark-grayish-green;
        }

        .f-underline {
            grid-row: 2;

            background-color: $off-white;
            border-top: 2px solid $very-dark-gray;
        }

        .f-name {
            grid-row: 3;
            justify-self: start;
            
            font-size: 50px;
            font-weight: 700;
            font-style: italic;
            letter-spacing: -2px;
            line-height: 0px;
            

            margin-top: 10px;
        }
    }

    .foundation-description {
        grid-column: 2 / 4;
        grid-row: 2;

        height: 100%;

        display: grid;
        grid-template-columns: repeat(3, minmax(0, 1fr));

        .f-section1 {
            grid-column: 3;

            display: grid;
            grid-template-rows: 87px 20px minmax(0, 1fr);
            grid-template-columns: 36px minmax(0, 1fr);
            justify-self: center;

            opacity: 0;
            transform: translateY(150px);
            transition:
                opacity 1s ease-in-out,
                transform 1s ease-in-out;
            transition-delay: 100ms;

            &.open {
                opacity: 1;
                transform: translateY(0);
            }
    
            &.close {
                opacity: 0;
                transform: translateY(-150px);
            }

            .f1-header {
                grid-row: 1;
                align-self: end;

                display: grid;
                grid-template-columns: 35px minmax(0, 1fr);
                grid-template-rows: 17px 16px;

                h3 {
                    grid-column: 2;

                    font-size: 24px;
                    letter-spacing: -1px;
                    line-height: 17px;

                    span {
                        color: $dark-grayish-green;
                    }
                }

                h4 {
                    grid-column: 1;
                    grid-row: 2;
                    align-self: end;
                    justify-self: end;

                    font-size: 12px;
                    font-weight: 400;
                    line-height: 8px;
                }
            }

            ul {
                grid-row: 3;
                grid-column: 2;
                list-style: none;
                justify-self: start;

                li {
                    padding: 6px 0;

                    font-size: 12px;
                    text-transform: capitalize;
                    font-variant: small-caps;
                }
            }
        }

        .f-section2 {
            grid-column: 2;

            display: grid;
            grid-template-rows: 54px 33px minmax(0, 1fr);

            opacity: 0;
            transform: translateY(150px);
            transition:
                opacity 1s ease-in-out,
                transform 1s ease-in-out;
            transition-delay: 200ms;

            &.open {
                opacity: 1;
                transform: translateY(0);
            }
    
            &.close {
                opacity: 0;
                transform: translateY(-150px);
            }

            .f2-box {
                z-index: 1;
                grid-row: 2 / 4;
                align-self: start;
                justify-self: center;

                width: $box-width;
                height: $box-width;

                background-color: $dark-grayish-green;

                display: grid;
                grid-template-rows: 33px 20px minmax(0, 1fr) 20px 10px;

                .f2-header {
                    z-index: 2;
                    grid-row: 1;
                    align-self: end;
                    justify-self: center;

                    font-size: 24px;
                    font-weight: 400;
                    line-height: 16px;
                    white-space: nowrap;
                }

                ul {
                    z-index: 2;
                    grid-row: 3;
                    justify-self: center;
                    align-self: center;
                    list-style: none;

                    li {
                        padding: 6px 0 6px 20px;

                        font-size: 12px;
                        text-transform: capitalize;
                        font-variant: small-caps;
                    }
                }

                .small-box {
                    z-index: 2;
                    grid-row: 4;

                    width: 20px;
                    height: 20px;

                    background-color: $off-white;

                    animation: slide-repeat 5s ease-in-out infinite;
                    @keyframes slide-repeat {
                        0% { transform: translateX(0) rotate(0deg);}
                        50% { transform: translateX(180px) rotate(360deg);}
                        100% { transform: translateX(0) rotate(0deg);}
                    }
                }
            }
        }

        .f-section3 {
            grid-column: 1;

            display: grid;
            grid-template-rows: 51px minmax(0, 1fr);

            opacity: 0;
            transform: translateY(150px);
            transition:
                opacity 1s ease-in-out,
                transform 1s ease-in-out;
            transition-delay: 300ms;

            &.open {
                opacity: 1;
                transform: translateY(0);
            }
    
            &.close {
                opacity: 0;
                transform: translateY(-150px);
            }

            .f3-content {
                grid-row: 2;
                align-self: start;
                justify-self: center;
                width: 80%;

                font-size: 12px;
                text-transform: none;
                font-variant: small-caps;
            }
        }
    }
}

@media (max-width: 1100px) {
    .foundation-container {
        .foundation-title {
            .f-name {
                font-size: 40px;
                margin-top: 7px;
            }
        }

        .foundation-description {
            .f-section2 {
                justify-self: end;
            }
        }
    }
}

@media (max-width: 950px) {
    .foundation-container {

        .foundation-title {
            .f-name {
                font-size: 30px;
                margin-top: 4px;
            }
        }

        .foundation-description {
            .f-section1 {
                .f1-header {
                    h3 {
                        font-size: 20px;
                    }
                }
            }

            .f-section2 {
                .f2-box {
                    width: 170px;
                    height: 170px;

                    .f2-header {
                        font-size: 20px;
                    }

                    .small-box {
                        @keyframes slide-repeat {
                            0% { transform: translateX(0) rotate(0deg);}
                        50% { transform: translateX(150px) rotate(360deg);}
                        100% { transform: translateX(0) rotate(0deg);}
                        }
                    }
                }
            }
        }
    }
}